@import "../../utils/theme";

.search-results {
  margin-top: 100px;

  svg {
    margin: 32px auto 64px auto;
  }

  h2 {
    color: $primary-text;
    font-size: 2rem;
    margin: 32px 0;
    text-align: center;
  }

  .results-card-group {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 24px;

    .results-card {
      width: 300px;
      height: 420px;
      margin: auto;
      padding: 24px;
      border-radius: 8px;

      background-color: #fff;

      display: flex;
      flex-direction: column;
      align-items: inline-start;
      justify-content: left;

      transition: all 0.3s linear;

      &:hover {
        border-radius: 16px;
        box-shadow: 0 0 0 2px #2b3897, 0 0 15px 6px #444;
      }

      .card-image {
        height: 300px;

        img {
          width: 100%;
        }
      }

      .pills {
        span {
          font-size: 0.8rem;
        }

        .pill {
          font-size: 0.8rem;
          text-transform: capitalize;

          padding: 8px 16px;
          border-radius: 16px;
          border: none;

          color: #fff;
          background-color: #2b3897;
          transition: all 0.2s linear;

          &:hover {
            background-color: #bb4dc3;
          }
        }

        .pill + .pill {
          margin-left: 8px;
        }
      }
    }

    h6 {
      font-size: 1.4rem;
      text-transform: capitalize;
      margin: 16px 0;
    }
  }

  .pagination {
    list-style-type: none;
    width: 80%;
    max-width: 420px;
    margin: 32px auto;

    cursor: pointer;
    display: flex;
    justify-content: space-between;

    li {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #fafafa;

      width: 40px;
      height: 40px;
      border-radius: 4px;

      &:hover {
        background-color: #eae9e9;
      }
    }

    .selected {
      background-color: #eae9e9;
    }

    a {
      padding: 16px;
    }
  }

  .change-page {
    width: 12px;
  }
}
