@import "../../utils/theme";

footer {
  display: flex;
  align-items: center;
  justify-content: center;

  padding: 16px 0 24px 0;
  background-color: $primary-text;
  width: 100vw;
  position: absolute;
  left: 0;
  overflow: hidden;

  img {
    width: 48px;
    margin: 0 16px;
  }

  p {
    font-size: 2rem;
    color: #fff;
    font-family: "Rubik Dirt", cursive;
    font-style: normal;
    font-weight: normal;
  }
}
