.youtube-results {
  svg {
    margin: 32px auto 16px auto;
  }

  h3 {
    margin: 1.6rem 0;
  }

  .youtube-video-card {
    margin: 2em 0;

    iframe {
      border: none;
      width: 100%;
      aspect-ratio: 8 / 5;
    }

    .youtube-video-card-link {
      position: relative;
    }

    .youtube-video-card-thumb {
      width: 100%;
      filter: brightness(50%);
    }

    .play-icon {
      position: absolute;
      width: 20%;
      left: 40%;
      top: 30%;
    }

    h4 {
      font-size: 1.1em;
      margin: 0.4rem 0;
    }

    p {
      font-size: 0.9em;
    }
  }

  @media only screen and (min-width: 600px) {
    h3 {
      margin: 5rem 0 3rem 0;
      text-align: center;
      font-size: 1.4rem;
    }

    .youtube-video-card {
      margin: 2em auto;
      width: 60%;
    }
  }

  @media only screen and (min-width: 1200px) {
    h3 {
      margin: 6rem 0 1rem 0;
      text-align: left;
      font-size: 1.8rem;
    }

    .youtube-videos {
      display: flex;
      align-items: top;
      justify-content: space-between;
    }

    .youtube-video-card {
      width: 30%;
      margin: 2em 0;

      h4 {
        font-size: 1.2em;
        margin: 0.8rem 0 0.5rem 0;
      }

      p {
        font-size: 1em;
      }
    }
  }
}
