$background: #fafafa;
$primary-text: #19215e;
$secondary-text: #373640;

@mixin button {
  font-size: 1.4rem;
  padding: 0.7em 1em;
  border-radius: 8px;
  background-color: $primary-text;
  color: $background;
  border: 1px solid $primary-text;
  transition: all 0.3s linear;

  &:hover,
  &:active {
    background-color: $background;
    color: $primary-text;
    border: 1px solid $primary-text;
    box-shadow: 0 0 4px $primary-text;
  }
}
