@import "../../utils/theme";

.searchExercises {
  margin: 100px 0 70px 0;

  h2 {
    margin: 40px 0;
    text-align: center;
    font-size: 2rem;
    font-weight: 500;
  }

  .search {
    position: relative;
    width: min(90%, 450px);
    margin: 0 auto;

    input {
      display: block;
      width: 100%;
      padding: 14px 12px;
      font-size: 1rem;
      border-radius: 4px;
      border: 1px solid #000;
    }

    button {
      @include button;
      position: absolute;
      top: 0;
      right: 0;
      font-size: 1rem;
      padding: 14px 20px;
      border-radius: 4px;
    }
  }
}

@media only screen and (min-width: 1080px) {
  .searchExercises {
    h2 {
      margin: 50px 0;
      font-size: 2.6rem;
    }

    .search {
      width: 600px;

      input {
        font-size: 1.2rem;
        padding: 18px 12px;
      }

      button {
        font-size: 1.2rem;
        padding: 18px 28px;
      }
    }
  }
}
