@import "../../utils/theme";

header {
  margin: 10px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .logo {
    width: 4rem;
    cursor: pointer;
  }

  nav {
    margin-left: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  a {
    font-size: 1.6em;
    border-bottom: 4px solid transparent;
    margin-right: 40px;
  }

  nav a:first-child {
    border-bottom-color: $primary-text;
  }
}

@media only screen and (min-width: 600px) {
  header {
    max-width: 450px;
    padding-left: 30px;
  }
}

@media only screen and (min-width: 1200px) {
  header {
    height: 10vh;
  }
}
