@import "../../utils/theme";

.body-parts {
  .loader {
    margin: 0 auto;
    width: min-content;
  }

  .body-part-card {
    min-width: 200px;
    min-height: 200px;
    background-color: #b3acf4;
    color: $secondary-text;
    text-transform: capitalize;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    margin: 20px;

    transition: all 0.3s linear;

    &:hover {
      box-shadow: 0 0 0 4px #000, 0 0 15px 6px #444;
    }

    img {
      width: 100px;
    }
  }

  .arrow {
    width: 60px;
    display: flex;
    align-items: center;
    justify-content: center;

    img {
      width: 40px;
    }
  }

  @media only screen and (min-width: 1200px) {
    .body-part-card {
      min-width: 220px;
      min-height: 220px;
    }
  }
}
