@import "./utils/theme";

html {
  scroll-behavior: smooth;
}

body {
  min-width: 360px;
  background-color: $background;
  padding: 10px 20px;
  min-height: 100vh;
  overflow-x: hidden;
  color: $secondary-text;
}

a {
  text-decoration: none;
  color: $primary-text;
}

.error {
  width: fit-content;
  margin: auto;
  color: red;
}

@media screen and (min-width: 600px) {
  body {
    padding: 20px 30px;
  }
}

@media only screen and (min-width: 1200px) {
  body {
    padding: 30px 80px;
  }
}
