@import "../utils/theme";

.exercise-details {
  padding: 20px;
  padding-bottom: 0;

  .exercise-image {
    display: block;
    margin: 24px auto;
    width: 100%;
  }

  .exercise-name-heading {
    padding: 24px 0;
    font-size: 2rem;
    text-transform: capitalize;
  }

  .exercise-paragraph {
    font-size: 1.1rem;
    line-height: 1.5;
    text-align: justify;
  }

  .key-details {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
    margin: 48px;
  }

  .key-detail-item {
    display: flex;
    align-items: center;
    width: 240px;

    .detail-item-icon {
      display: flex;
      align-items: center;
      justify-content: center;

      padding: 16px;
      margin-right: 32px;
      border-radius: 50%;
      background-color: #646fc2;

      img {
        width: 46px;
      }
    }

    h5 {
      font-size: 1.2rem;
      text-transform: capitalize;
    }
  }

  .error-div {
    margin: 80px 0;

    .dead-img {
      width: min(45vw, 240px);
      display: block;
      margin: 0 auto;
      animation: sway 1s linear infinite;

      @keyframes sway {
        0% {
          transform: translate(0);
        }
        25% {
          transform: translate(-20px) rotate(-10deg);
        }
        75% {
          transform: translate(20px) rotate(10deg);
        }
        100% {
          transform: translate(0);
        }
      }
    }

    .error {
      margin: 64px auto;
      font-size: 1.1em;
    }
  }

  .youtube-results {
    .exercise-name {
      color: $primary-text;
      text-transform: capitalize;
    }
  }

  @media screen and (min-width: 768px) {
    .key-details {
      flex-direction: row;
    }

    .exercise-image {
      width: min(40vw, 480px);
      height: min(40vw, 480px);
    }
  }

  @media screen and (min-width: 992px) {
    .exercise-main-section {
      display: flex;

      .text-segment {
        margin: 0 32px;
      }

      .key-details {
        flex-direction: column;
        margin: 18px;
      }
    }
  }

  @media screen and (min-width: 1200px) {
    .exercise-main-section .key-details {
      margin: 48px;
    }
  }
}
