@import "../../utils/theme";

.hero {
  h1 {
    margin-top: 50px;
    font-size: 2.2rem;
    color: $primary-text;
    font-family: "Rubik Dirt", cursive;
    font-style: normal;
    font-weight: normal;
  }

  .slogan {
    margin: 40px 0;
    font-size: 1.8rem;
    font-weight: 500;
  }

  p {
    margin: 40px 0;
    font-size: 1.2rem;
    line-height: 1.6;
    color: $secondary-text;
  }

  button {
    @include button;
  }

  .hero-img {
    display: none;
  }
}

@media screen and (min-width: 600px) {
  .hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;

    h1 {
      font-size: 3.6rem;
    }

    .slogan {
      font-size: 2rem;
    }
  }
}

@media only screen and (min-width: 992px) {
  .hero {
    min-height: 70vh;
    align-items: flex-start;

    h1 {
      font-size: 5rem;
    }

    .hero-img {
      display: block;
      height: 120vh;
      position: absolute;
      // left: 55vw;
      left: 600px;
      top: 0;
      clip-path: polygon(0 0, 100% 0, 100% 100%, 10% 88%);
      z-index: -1;
    }
  }
}
